		descriptions: [
			{content: 'Чарли - тучный и замкнутый профессор, который преподает онлайн-курсы в колледже. Впрочем, он всегда держит свою веб-камеру выключенной, боясь показать свою внешность студентам. За Чарли ухаживает его медсестра и единственная подруга Лиз, которая настоятельно рекомендует ему посетить больницу, поскольку у него серьезный риск сердечной недостаточности. Его также посещает Томас, миссионер церкви «Новая жизнь». Томас пытается проповедовать Чарли Евангелие. Профессор часто заказывает пиццу, для чего он и его курьер доставки Дэн придумали даже алгоритм: Ден оставляет пиццу на крыльце, берет деньги из почтового ящика и уходит, даже не пообщавшись с Чарли.'},
			{content: 'Чарли также пытается наладить отношения со своей дочерью-подростком Элли, которая уже восемь лет живет со своей матерью отдельно от Чарли. Чарли предлагает ей все деньги со своего банковского счета, чтоб Элли проводила с ним время без ведома ее матери. Элли соглашается, но только в том случае, если Чарли поможет ей переписать сочинение для школы. Чарли готов на это. В обмен на ее компанию Элли должна делать записи в блокноте, который он ей дает.'},
			{content: 'Лиз недовольна частыми визитами Томаса и говорит ему, что Чарли не нуждается в спасении. Лиз рассказывает, что она приемная дочь главного пастора «Новой жизни» и что Алан, покойный любовник Чарли, чья смерть от самоубийства из-за религиозных предрассудков привела Чарли к бесконтрольному обжорству, был ее братом. Чарли так и не оправился полностью от этой потери. Несмотря на возражения Лиз, Томас по-прежнему верит, что его миссия состоит в том, чтобы помочь Чарли.'},
			{content: 'Здоровье Чарли начинает ухудшаться после того, как он чуть не подавился сэндвичем, и Лиз доставляет ему инвалидное кресло, чтобы ему было легче передвигаться по квартире. Однажды Элли тайно подсыпает снотворное в очередной сэндвич, который она делает для Чарли. После того, как он засыпает, приходит Томас, и Элли начинает расспрашивать его про прошлое, пока он курит травку. Поначалу поколебавшись, Томас признается, что сбежал из своей семьи и старой миссии в Айове после того, как почувствовал, что ему чего-то не хватает. Томас украл все деньги у молодежной группы сообщества. Элли тайно записывает его признание на свой телефон.'},
			{content: 'Лиз приводит Мэри, бывшую жену Чарли и мать Элли, навестить его, и раскрывается правда о тайных встречах Элли с ее отцом. Происходит жаркий обмен мнениями, и Чарли сознается, что у него на банковском счете 120 000 долларов. Поскольку Чарли солгал Лиз о том, что у него нет денег на здравоохранение, та в гневе выбегает, оставляя Мэри и Чарли одних спорить о распаде их брака и их неудачах как родителей. Когда Мэри уходит, Чарли со слезами на глазах признается, что ему нужно знать, что в его жизни есть хотя бы одна вещь, которую он сделал правильно.'},
			{content: 'Томас навещает Чарли в последний раз, сообщая ему, что он возвращается домой после того, как Элли отправила его признание его бывшей группе и семье. Его семья прощает его за все, считая это мелочью, и ждет его домой. Томас пытается обратить Чарли в свою веру, но теряет доверие после того, как он говорит, что причина смерти Алана - его гомосексуальность. Чарли переживает тяжелый приступ обжорства после того, как Дэн впервые видит его мельком и рассылает своим ученикам электронное письмо, полное ненормативной лексики, с просьбой написать что-нибудь честное. На своем следующем занятии Чарли рассказывает, что его заменяют на посту их профессора, и читает некоторые из их откровенных мнений про внешность Чарли. Чтобы ответить взаимностью на их честность, он впервые включает свою веб-камеру и заканчивает урок, разбивая свой ноутбук об холодильник.'},
			{content: 'Чувствуя вину за то, что бросила Чарли, Лиз утешает его, когда Чарли находится при смерти. Элли врывается в квартиру, чтобы повздорить с ним из-за переписанного им эссе. Чарли рассказывает, что он заменил ее переписанное эссе критическим эссе о Моби Дике, которое она написала в восьмом классе, поскольку он считал это самым честным эссе, которое он когда-либо читал. Элли со слезами на глазах упрекает Чарли, когда он пытается помириться в последний раз. Он просит ее прочитать ему эссе. Элли читает эссе, в то время как Чарли встает и пытается подойти к ней, что он не смог сделать во время первого визита Элли. Когда Элли заканчивает чтение, их охватывает яркий белый свет, когда они вспоминают посещение пляжа всей семьёй.'},
			
		],
